@font-face {
  font-family: 'Roboto', sans-serif;
  src: url('fonts/Roboto/Roboto-Regular.ttf') format('truetype');
  src: url('fonts/Roboto/Roboto-Regular.woff') format('truetype');
  src: url('fonts/Roboto/Roboto-Regular.woff2') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}


@font-face {
  font-family: 'Roboto', sans-serif;
  src: url('fonts/Roboto/Roboto-Medium.ttf') format('truetype');
  src: url('fonts/Roboto/Roboto-Medium.woff') format('truetype');
  src: url('fonts/Roboto/Roboto-Medium.woff2') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}


@font-face {
  font-family: 'Roboto', sans-serif;
  src: url('fonts/Roboto/Roboto-Bold.ttf') format('truetype');
  src: url('fonts/Roboto/Roboto-Bold.woff') format('truetype');
  src: url('fonts/Roboto/Roboto-Bold.woff2') format('truetype');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}



